import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Http, Headers } from '@angular/http';
import { AppConfig } from '../../../app/app.config';
import 'rxjs/add/operator/map';

/**
 * Generated class for the ActicityaddPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-acticityadd',
    templateUrl: 'acticityadd.html',
})
export class ActicityaddPage {
    peopleurl = AppConfig.getUrl() + "/xfwSpider/xueshiusers?cookie=" + localStorage.xfwcookie;
    newurl = AppConfig.getUrl() + "/xfwSpider/shyk_save"
    nocomename: any;
    nocomereason: any;
    nocomenum: any;
    min: any;
    max: any;
    table: any = {
        cookie: "",
        cookie_djy: "",
        shykType: "",
        tips: "",
        shijianString: "",
        didian: "",
        zhuchiren: "",
        jiluren: "",
        yingdaoren: "",
        shidaoren: "",
        title: "",
        liexiren: "",
        content: "",
        cpc_user_name: "",
        cpc_user_id: "",
        cpc_absentuser_id: ""
    };

    constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
        this.max = this.time(new Date())
        var x = new Date();
        var y = x.setDate(x.getDate() - 15)
        this.min = this.time(new Date(y))
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad ActicityaddPage');
        if (this.navParams.data.id || this.navParams.data.shykType) {
            console.log(1)
        }
    }

    people(alerttype, alerttitle) {
        this.nocomename = [];
        this.nocomereason = [];
        this.nocomenum = [];
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        loading.present();
        return new Promise((resolve, reject) => {
            this.http.get(this.peopleurl)
                .map(res => res.json())
                .subscribe(data => {
                    resolve(data);
                    if (data.status) {
                        var names;
                        var peoplearr = [];
                        let alert = this.alertCtrl.create();
                        alert.setTitle(alerttitle);
                        for (let i = 0; i < data.data.length; i++) {
                            alert.addInput({
                                type: alerttype,
                                label: data.data[i].name,
                                value: data.data[i].name,
                            });
                            names = names + "," + data.data[i].name
                            peoplearr.push({
                                name: data.data[i].name,
                                id: data.data[i].id
                            })
                        }
                        names = names.split(",");
                        names.splice(0, 1)
                        alert.addButton('取消');
                        alert.addButton({
                            text: '确定',
                            handler: data => {
                                if (alerttitle == '选择主持人') {
                                    this.table.zhuchiren = data
                                }
                                if (alerttitle == '选择记录人') {
                                    this.table.jiluren = data
                                }
                                if (alerttitle == '选择参加人员') {
                                    this.table.cpc_user_name = data.toString();

                                    for (let i = 0; i < data.length; i++) {
                                        if (names.indexOf(data[i]) > -1) {
                                            names.splice(names.indexOf(data[i]), 1)
                                        }
                                    }

                                    for (let i = 0; i < names.length; i++) {
                                        this.nocomename.push(names[i])
                                        this.nocomereason.push("")
                                        this.nocomenum.push(i)
                                    }

                                    var addpeoplearr = [];
                                    for (let i = 0; i < data.length; i++) {
                                        for (let x = 0; x < peoplearr.length; x++) {
                                            if (peoplearr[x].name.indexOf(data[i]) > -1) {
                                                addpeoplearr.push(peoplearr.splice(x, 1))
                                            }
                                        }
                                    }
                                    for (let i = 0; i < peoplearr.length; i++) {
                                        this.table.cpc_absentuser_id = this.table.cpc_absentuser_id + peoplearr[i].id + ","
                                    }
                                    for (let i = 0; i < addpeoplearr.length; i++) {
                                        this.table.cpc_user_id = this.table.cpc_user_id + addpeoplearr[i][0].id + ","
                                    }
                                }
                            }
                        });
                        alert.present();
                    } else {
                        let alert = this.alertCtrl.create({
                            title: data.msg
                        });
                        alert.present();
                    }
                }, err => {
                    let alert = this.alertCtrl.create({
                        title: err
                    });
                    alert.present();
                })
            loading.dismiss();
        })
    }

    type() {
        let alert = this.alertCtrl.create();
        let txts=["支部党员会","支部委员会","党小组会","党课"]
        alert.setTitle("选择会议类型");
        alert.addInput({
            type: 'radio',
            label: '支部党员会',
            value: '0',
        });
        alert.addInput({
            type: 'radio',
            label: '支部委员会',
            value: '1',
        });
        alert.addInput({
            type: 'radio',
            label: '党小组会',
            value: '2',
        });
        alert.addInput({
            type: 'radio',
            label: '党课',
            value: '3',
        });
        alert.addButton('取消');
        alert.addButton({
            text: '确定',
            handler: data => {
                this.table.shykType = data
                this.table.shykType_ = txts[Number.parseInt(data)||0]
            }
        });
        alert.present();
    }
    tip() {
        let txts=["无","两学一做","中心组学习","四个意识专题教育","精神文明创建","党员志愿服务"];
        let alert = this.alertCtrl.create();
        alert.setTitle("选择标签");
        alert.addInput({
            type: 'radio',
            label: '无',
            value: '0',
        });
        alert.addInput({
            type: 'radio',
            label: '两学一做',
            value: '1',
        });
        alert.addInput({
            type: 'radio',
            label: '中心组学习',
            value: '2',
        });
        alert.addInput({
            type: 'radio',
            label: '四个意识专题教育',
            value: '3',
        });
        alert.addInput({
            type: 'radio',
            label: '精神文明创建',
            value: '4',
        });
        alert.addInput({
            type: 'radio',
            label: '党员志愿服务',
            value: '5',
        });
        alert.addButton('取消');
        alert.addButton({
            text: '确定',
            handler: data => {
                this.table.tips = data
                this.table.tips_ = txts[Number.parseInt(data)||0]
            }
        });
        alert.present();
    }
    sub() {
        if (!this.table.shykType ||
            !this.table.tips ||
            !this.table.shijianString ||
            !this.table.didian ||
            !this.table.zhuchiren ||
            !this.table.jiluren ||
            !this.table.yingdaoren ||
            !this.table.shidaoren ||
            !this.table.title ||
            !this.table.content) {
            let alert = this.alertCtrl.create({
                title: '请填写全部内容才能进行提交!'
            });
            alert.present();
        } else {
            let alert = this.alertCtrl.create();
            alert.setTitle("提交选项");
            alert.addInput({
                type: 'checkbox',
                label: '提交至先锋网',
                value: '0',
            });
            alert.addInput({
                type: 'checkbox',
                label: '提交至党建云',
                value: '1',
            });
            alert.addButton('取消');
            alert.addButton({
                text: '确定',
                handler: data => {
                    if (data.indexOf("0") > -1) {
                        this.table.cookie = localStorage.xfwcookie
                    }
                    if (data.indexOf("1") > -1) {
                        this.table.cookie_djy = localStorage.djycookie
                    }
                    this.sub2()
                }
            });
            alert.present();
        }
    }
    sub2() {
        console.log(this.table)
        var x = "";
        for (let i = 0; i < this.nocomename.length; i++) {
            x = "&model.cpc_absentuser_name=" + this.nocomename[i] + "&model.quexireason=" + this.nocomereason[i] + x;
        }
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        console.log(x)
        loading.present();
        let header = new Headers();
        header.append("Content-Type", "application/x-www-form-urlencoded");
        return new Promise((resolve, reject) => {
            this.http.post(this.newurl, this.transformAsUrlEncode(this.table) + x, { headers: header })
                .map(res => res.json())
                .subscribe(data => {
                    resolve(data)
                    console.log(data)
                    if (data.status) {
                        let alert = this.alertCtrl.create({
                            title: "提交成功"
                        });
                        alert.addButton({
                            text: '确定',
                            handler: () => {
                                this.navCtrl.pop()
                            }
                        });
                        alert.present();
                    } else {
                        let alert = this.alertCtrl.create({
                            title: data.msg
                        });
                        alert.present();
                    }
                }, err => {
                    reject(err)
                    console.log(err);
                    let alert = this.alertCtrl.create({
                        title: '服务器连接失败!'
                    });
                    alert.present();
                })
            loading.dismiss();
        })
    }
    transformAsUrlEncode(params): string {
        let urlEncodeParams = "";
        for (let key in params) {
            if (key == "imgcode") {
                //imgcode参数是app本地使用的参数，且size较大，在上传时将其排除
                continue;
            }
            let param = key + "=" + params[key] + "&";
            param = param.replace(/\+/g, "%2B");//base64图片中+号会变空格，处理下
            urlEncodeParams += param;
        }
        return urlEncodeParams;
    }
    time(date) {
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
    }

}



